/**
 * 创建表格入口
 * @param options 表格参数
 * @param options.container 表格渲染区域，可选dom节点以及选择器
 * @returns 表格实例
 */
export function createSheet(options: Partial<AxisSheet['props']> & { container: HTMLElement | string }): AxisSheet {
    const { container, ...args } = options;

    return BI.createWidget({
        ...args,
        type: AxisSheet.xtype,
        element: container,
    });
}
